body {
  word-wrap: break-word;
}
p {
  font-family: "Poppins", sans-serif;
  font-weight: 300;
  font-size: 14px;
  line-height: 24px;
  color: #6f7982;
}
.home-page {
  height: 100%;
  max-width: 1200px;
  position: relative;
  margin: auto;
}
.header {
  width: 100%;
  position: absolute;
  z-index: 1;
  top: 0;
}
.header .home-page {
  padding: 0 24px;
}
.header-logo {
  display: inline-block;
  position: relative;
  width: 150px;
  line-height: 64px;
}
.header-menu {
  float: right;
}
.header-menu .ant-menu-horizontal {
  border-bottom: none;
}
.header-menu .ant-menu {
  color: #ebebeb;
  line-height: 62px;
  height: 64px;
  background: transparent;
}
.header-menu .ant-menu-item-selected,
.header-menu .ant-menu-item:hover,
.header-menu .ant-menu-item-active,
.header-menu .ant-menu-submenu-active {
  color: white;
  border-bottom: 2px solid white !important;
}
.header-menu .ant-menu-submenu-title:hover {
  color: white;
}
@media screen and (max-width: 767px) {
  .header-logo {
    z-index: 101;
  }
  .header .home-page {
    padding: 0 24px;
  }
  .header-menu {
    height: auto;
    float: inherit;
    position: relative;
    left: -24px;
    width: calc(100% + 48px);
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
    background: #fff;
  }
  .header-mobile-menu {
    width: 16px;
    height: 14px;
    cursor: pointer;
    position: absolute;
    top: 24px;
    right: 24px;
    z-index: 100;
  }
  .header-mobile-menu em {
    display: block;
    width: 100%;
    height: 2px;
    margin-top: 4px;
    transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
  }
  .header-mobile-menu :first-child {
    margin-top: 0;
  }
  .header .ant-menu {
    height: auto;
    overflow: hidden;
  }
  .header .ant-menu .ant-menu-item-selected {
    border: none;
  }
  .header .open {
    height: auto;
  }
  .header .open .header-mobile-menu em:nth-child(1) {
    transform: translateY(6px) rotate(45deg);
  }
  .header .open .header-mobile-menu em:nth-child(2) {
    opacity: 0;
  }
  .header .open .header-mobile-menu em:nth-child(3) {
    transform: translateY(-6px) rotate(-45deg);
  }
  .header .open > .header-menu {
    opacity: 1;
    pointer-events: auto;
  }
}
.banner {
  width: 100%;
  height: 812px;
  position: relative;
  background-image: url(/frinxio/static/media/banner1.10eb6366.jpg);
  overflow: hidden;
}
.banner .banner-page {
  max-width: 1300px;
}
.banner-title {
  font-size: 48px;
  line-height: 1.2;
  color: #ffffff;
  margin-bottom: 5px;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
}
.banner-title-wrapper {
  position: relative;
  top: 28%;
  width: 50%;
  max-width: 550px;
  left: 70px;
  z-index: 3;
}
.banner-explain {
  font-size: 24px;
  margin-bottom: 24px;
  color: white;
}
.banner-release {
  color: white;
  font-size: 24;
  padding: 5px;
  padding-left: 10px;
  width: 75%;
  border: 1px solid white;
  border-radius: 20px;
  margin-bottom: 10px;
  cursor: pointer;
  transition: width 0.1s ease-in-out;
}
.banner-release:hover {
  width: 78%;
}
.banner-content {
  font-size: 18px;
  color: #e9e9e9;
  margin-bottom: 48px;
  line-height: 1.2;
  font-family: Quicksand, sans-serif;
}
.banner-button {
  background-color: white !important;
  margin-right: 10px !important;
}
.banner-image {
  overflow: hidden;
  width: 65%;
  position: absolute;
  top: 25%;
  left: 50%;
  z-index: 1;
}
@media screen and (max-width: 767px) {
  .banner {
    min-height: 500px;
  }
  .banner .banner-page.home-page {
    padding-top: 1px;
    padding-bottom: 0;
  }
  .banner-title-wrapper {
    text-align: center;
    position: static;
    width: 100%;
    margin-top: 64px;
    left: 0;
    top: 0;
  }
  .banner-title {
    font-size: 32px;
  }
  .banner-explain {
    font-size: 18px;
  }
  .banner-content {
    font-size: 14px;
  }
  .banner-release {
    display: none;
  }
  .banner-particles {
    display: none;
  }
  .banner-image {
    position: static;
    width: 100%;
    top: 0;
    right: 0;
    margin: 32px 0 24px;
  }
}
