.joinus-wrapper {
  display: flex;
  background-image: url(/frinxio/static/media/banner1.10eb6366.jpg);
  align-items: center;
  justify-content: center;
  height: 70px;
}
.pulse-bg,
.pulse-button {
  position: absolute;
  width: 130px;
  height: 40px;
  border-radius: 50px;
}
.pulse-bg {
  -webkit-animation: pulse 1.2s ease infinite;
          animation: pulse 1.2s ease infinite;
  background: white;
}
.pulse-button {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  z-index: 99;
  border: none;
  background: white;
  background-size: 15px;
  cursor: pointer;
  outline: none;
}
@-webkit-keyframes pulse {
  0% {
    transform: scale(1, 1);
  }
  50% {
    opacity: 0.3;
  }
  100% {
    transform: scale(1.5);
    opacity: 0;
  }
}
@keyframes pulse {
  0% {
    transform: scale(1, 1);
  }
  50% {
    opacity: 0.3;
  }
  100% {
    transform: scale(1.5);
    opacity: 0;
  }
}
