.timeline-wrapper {
  padding-bottom: 24px;
  padding-top: 64px;
  margin-left: 10px;
}
.timeline-wrapper.home-page-wrapper {
  overflow: visible;
  overflow: initial;
}
.timeline-wrapper .timeline {
  min-height: 800px;
}
.timeline-wrapper .timeline {
  position: relative;
}
.timeline-wrapper .timeline:before {
  display: block;
  content: "";
  position: absolute;
  left: 50%;
  top: -20%;
  width: 0;
  z-index: -1;
  height: calc(100% + 150px);
  margin: 34px 0;
  border-left: 2px #ebedf0 dashed;
}
.timeline-wrapper .block-wrapper {
  color: rgba(0, 0, 0, 0.85);
  display: flex;
  position: relative;
  margin-bottom: 70px;
  min-height: 160px;
}
.timeline-wrapper .block-wrapper:last-child {
  margin-bottom: 0;
}
.timeline-wrapper .image-wrapper,
.timeline-wrapper .text-wrapper {
  width: 50%;
}
.timeline-wrapper .image-wrapper {
  text-align: center;
}
.timeline-wrapper .image-wrapper .name-wrapper {
  float: right;
  clear: both;
  width: 262px;
  margin: auto;
}
.timeline-wrapper .image-wrapper .block-img {
  height: 250px;
  margin: auto;
}
.timeline-wrapper .image-wrapper .block-img img {
  height: 100%;
}
.timeline-wrapper .text-wrapper {
  padding: 40px;
}
.timeline-wrapper .text-wrapper .block-title {
  margin: 8px 0 12px;
  font-size: 24px;
  position: relative;
  min-height: 18px;
  line-height: 1.2;
  font-weight: 600;
  font-family: Quicksand, sans-serif;
}
.timeline-wrapper .text-wrapper .block-icon {
  position: absolute;
  left: -40px;
  transform: translateX(calc(-50% + 1px));
}
.timeline-wrapper .text-wrapper .block-content {
  width: 100%;
  font-family: "Poppins", sans-serif;
  font-weight: 300;
  font-size: 14px;
  color: #6f7982;
}
@media screen and (max-width: 767px) {
  .timeline-wrapper {
    padding-bottom: 0;
  }
  .timeline-wrapper .timeline:before {
    left: 0;
    height: 100%;
    margin: 0;
  }
  .timeline-wrapper .block-wrapper {
    display: block;
    min-height: 100px;
    margin-bottom: 54px;
  }
  .timeline-wrapper .image-wrapper,
  .timeline-wrapper .text-wrapper {
    width: 100%;
  }
  .timeline-wrapper .image-wrapper .block-img {
    display: none;
  }
  .timeline-wrapper .image-wrapper .name-wrapper {
    text-align: left;
    float: left;
    width: 100%;
    padding-left: 40px;
    margin: auto;
  }
  .timeline-wrapper .text-wrapper {
    padding: 0 40px 0 40px;
  }
  .timeline-wrapper .text-wrapper .block-title {
    margin: 8px 0 12px;
    font-size: 24px;
    position: relative;
    min-height: 18px;
  }
  .timeline-wrapper .text-wrapper .block-icon {
    position: absolute;
    left: -40px;
    transform: translateX(calc(-50% + 1px));
  }
  .timeline-wrapper .text-wrapper .block-content {
    width: 100%;
    color: #314659;
    font-size: 14px;
    min-height: 18px;
  }
}
